import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
// import {TranslateModule} from '@ngx-translate/core';

import {IlRoutingModule} from './il.routing';
import {IlComponent} from './il.component';
import {SidebarComponent, HeaderComponent} from '../shared';
import {OverallAnalysisService} from "./overall-analysis/service/overall-analysis-service.";
import {HttpModule} from "@angular/http";
import { YscComponentModule } from 'ysc-pop-component';
import {AuthClient} from "../shared/services/authclient";
@NgModule({
  imports: [
    CommonModule,
    IlRoutingModule,
    // TranslateModule, 
    HttpModule,
      YscComponentModule
  ],
  declarations: [
    IlComponent,
    SidebarComponent,

  ],
   providers: [OverallAnalysisService,AuthClient]
})
export class IlModule {
}



